import React from "react";
import { Image, Nav, Navbar } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import '../assets/styles/header.css';
import logo from '../assets/images/216.png'; 

export default function Header(props) {
  return (
    <div >

      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Navbar.Brand href="/bio">React-Bootstrap</Navbar.Brand>
        <Link to="/bio" className="nav-link shadow-lg p-1 bg-white rounded" >
          <Image src={logo} rounded height="100" />
        </Link>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="ml-auto p-2">
            <Link to="/APage" className="nav-link text-light" activeclassname="active" > Home </Link>
            <Link to="/BPage" className="nav-link text-light" activeclassname="active" > Machine learning </Link>
            <Link to="/CPage" className="nav-link text-light" activeclassname="active" > Microservices </Link>
            <Link to="/EPage" className="nav-link text-light" activeclassname="active" > About Me  </Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </div>
  );
};